@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
	font-size: 62.5%;

	// блокировка скролла
	&.css-scroll-fixed:not(.open-only-mob) {
		@include scroll-blocked; }

	&.css-scroll-fixed.open-only-mob {
		@include media('<tab-lg') {
			@include scroll-blocked; } } }

/* === LAYOUT === */
.max-wrap {
	max-width: $page-layout-width; }

.layout {
	@include padding-horizontal(nth($page-layout-space, 2));
	@include push-auto;

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

.layout-space {
	@include padding-horizontal(nth($page-layout-space, 2));

	@include media('<mob-lg') {
		@include padding-horizontal(nth($page-layout-space--mob, 2)); } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
	//padding: $text-layout-space

	//+media('<tab-lg')
	//adding: $text-layout-space--md

	//+media('<tab-sm')
	//adding: $text-layout-space--sm

	& > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text, .imgc figcaption {
		max-width: $text-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	.layout-flood {
		max-width: $flood-layout-width;
		@include padding-horizontal(0);
		@include push-auto; }

	// элеметы без отступов по бокам
	// минусовые маржины по горизонтале имеют значение только внутри .layout-article
	.layout-full-width, .layout-wide {

		@include margin-horizontal(-#{nth($text-layout-space, 2)});

		@include media('<tab-lg') {
			@include margin-horizontal(-#{nth($text-layout-space--md, 2)}); }

		@include media('<tab-sm') {
			@include margin-horizontal(-#{nth($text-layout-space--sm, 2)}); } } }

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
	max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2};

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2}; }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}; } }

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
	max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal(nth($text-layout-space, 2));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal(nth($text-layout-space--md, 2)); }

	@include media('<tab-sm') {
		max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal(nth($text-layout-space--sm, 2)); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
	max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2};

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}; }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--mob, 2))*2}; } }

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
	max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2};
	@include padding-horizontal((nth($text-layout-space, 2)));
	@include push-auto;

	@include media('<tab-lg') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2};
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		max-width: #{$flood-layout-width + (nth($text-layout-space--sm, 2))*2};
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
	@include padding-horizontal((nth($text-layout-space, 2)));

	@include media('<tab-lg') {
		@include padding-horizontal((nth($text-layout-space--md, 2))); }

	@include media('<tab-sm') {
		@include padding-horizontal((nth($text-layout-space--sm, 2))); }

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

	p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
		max-width: none; } }

/* === layout over === */
// Если необходимо вытянуть элемент к краям сонтейнера
//[class*="layout-over"][class*="--bottom"]
//content &
//margin-bottom: -#{nth($content-space, 3)}

//[class*="layout-over"][class*="--top"]
//content &
//margin-top: -#{nth($content-space, 1)}

[class*="layout-over"][class*="--left"] {
	.layout &, .layout-space & {
		margin-left: -#{nth($page-layout-space, 2)};

		@include media('<mob-lg') {
			margin-left: -#{nth($page-layout-space--mob, 2)}; } } }

[class*="layout-over"][class*="--right"] {
	.layout &, .layout-space & {
		margin-right: -#{nth($page-layout-space, 2)};

		@include media('<mob-lg') {
			margin-right: -#{nth($page-layout-space--mob, 2)}; } } }

/* === layout over end === */

// layout страницы
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	margin: 0 auto;
	min-height: 100%;
	max-width: 100%;
	width: $page-layout-width;
	background-color: $bg-color-alt;
	box-shadow: 0 0 0 1px #cdcdcd; }

.article-footer {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;

	@include media('<mob-lg') {
		display: block; } }

.article-footer__col {
	display: inline-block;
	display: flex;

	@include media('<mob-lg') {
		display: block;
		text-align: center;
		margin-bottom: 2rem; }

	&:last-child {
		margin-bottom: 0; } }

/* === LAYOUT end === */

/* === CONTENT === */
.main {
	flex-shrink: 1;
	flex-grow: 1;
	padding-top: $header-height;

	@include media('<tab-lg') {
		padding-top: $header-height--sm; } }

.content {
	padding-top: nth($content-space, 1);
	padding-bottom: nth($content-space, 3);

	.card-page & {
		background-color: white; } }

.sheet {
	flex-shrink: 1;
	flex-grow: 1;
	background-color: #fff;
	border-radius: 10px;
	box-shadow: 0 3px 3px rgba(black, 0.1);
	max-width: $flood-layout-width;
	margin: 0 auto;
	padding: 38px nth($page-layout-space, 2);

	@include media('<mob-lg') {
		padding: 38px nth($page-layout-space--mob, 2);
		border-radius: 0;
		margin: -#{nth($content-space, 1)} -#{nth($page-layout-space--mob, 2)} -#{nth($content-space, 3)}; } }

/* === CONTENT end === */

/* === HEADER === */
.header {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: $header-height;
	min-width: $page-layout-min;
	z-index: $z-i-header;

	@include media('<tab-lg') {
		height: $header-height--sm; } }

.header__holder {
	position: relative;
	height: 100%;
	//padding: 0
	background-color: $header-bg;
	box-shadow: $header-shadow;
	@include clear; }

/* logo */
.logo {
	display: block;
	position: absolute;
	left: nth($page-layout-space, 2);
	width: nth($logo-size, 1);
	top: 50%;
	transform: translate(0, -50%);

	@include media('<mob-lg') {
		left: nth($page-layout-space--mob, 2);
		width: nth($logo-size--mob, 1); }

	&::before {
		content: "";
		display: block;
		@include ratio(nth($logo-size, 1), nth($logo-size, 2));
		background-repeat: no-repeat;
		background-position: left;
		background-color: $header-bg;
		background-image: url(../../img/logo.png);
		background-size: contain; }

	a, & > span {
		position: absolute;
		display: block;
		left: 0;
		top: 0;
		@include size(100%);
		z-index: 5;

		& > em {
			@include hide-text; } } }

/* logo end */

/* search-form */
.search-panel {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	min-width: 320px;
	z-index: 20;

	&.search-is-open {
		z-index: 25; } }

.search-panel__layout {
	margin: 0 auto;
	position: relative; }

.search-form {
	$bg-color: white;
	display: none;
	align-items: center;
	position: absolute;
	right: 0;
	top: 0;
	height: $header-height;
	padding: 5px 90px 5px nth($page-layout-space, 2);
	background-color: $bg-color;
	//opacity: 0
	//visibility: hidden
	// transition
	//z-index: -1
	//$prop: opacity
	//will-change: $prop
	//transition:
	//roperty: $prop
	//uration: $animation-speed

	@include media('<tab-lg') {
		height: $header-height--sm;
		padding-right: 70px;
		width: 100%; }

	@include media('<mob-lg') {
		padding-left: nth($page-layout-space--mob, 2); }

	&::before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		right: 100%;
		height: 100%;
		width: 40px;
		margin: 0 -4px;
		pointer-events: none;
		@include linear-gradient(to left, $bg-color 0%, $bg-color 50%, rgba($bg-color, 0) 100%);

		@include media('<tab-lg') {
			display: none; } }

	.search-is-open & {
		display: block;
		display: flex;
		//opacity: 1
 } }		//z-index: auto

.search-form__field {
	position: relative;
	height: nth($search-form--size, 1);
	width: nth($search-form--size, 2);
	margin: 0;
	// transform
	//$scale: 0.95
	//transform: scale($scale)
	//transform: scale3d($scale, $scale, 1)
	// transition
	//$prop: transform
	//will-change: $prop
	//transition:
	//roperty: $prop
	//uration: $animation-speed

	@include media('<tab-lg') {
		height: nth($search-form--size--sm, 1);
		width: nth($search-form--size--sm, 2); }

	.search-is-open & {
		// transform
		//$scale: 1
		//transform: scale($scale)
		//transform: scale3d($scale, $scale, 1)
		display: block; } }

input.search-form__input {
	@include size(100%);
	@include font-medium;
	@include font(16px, 20px);
	padding: 13px 45px 13px 10px;
	border-radius: 5px;
	border: 2px solid $border-color;
	background-color: #f3f3f3;

	@include media('<tab-lg') {
		@include padding-vertical(8px); }

	&, &:hover, &:focus, &.error, &.input--has-value {
		box-shadow: none;
		border-color: $border-color; } }

.search-form__btn {
	position: absolute;
	right: 10px;
	top: 50%;
	margin: -15px 0 0;
	@include font-normal;
	@include size(30px);
	color: $text-color;
	text-align: center;
	border: none;
	background: none;
	padding: 0;
	z-index: 5;
	// transition
	pointer-events: none;
	$prop: color;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	.input--has-value & {
		pointer-events: auto; }

	input {
		@include cover-abs();
		padding: 0;
		border: 0;
		opacity: 0;
		z-index: 99; }

	svg {
		display: inline-block;
		position: absolute;
		@include position(50%);
		@include size(18px);
		margin: -9px 0 0 -9px; }

	path {
		fill: currentColor;
		transition: inherit; }

	span {
		@include hide-text; }

	.no-touchevents &:hover {
		color: $link-color; } }

.search-opener {
	display: block;
	position: absolute;
	right: nth($page-layout-space, 2);
	top: round($header-height/2);
	margin: -#{round($search-form--opener-size/2)} 0 0;
	height: $search-form--opener-size;
	width: $search-form--opener-size;
	border: 2px solid $border-color;
	border-radius: $search-form--opener-size;
	background-color: white;
	color: $text-color;
	z-index: 20;
	$prop: color, border-color;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	@include media('<tab-lg') {
		top: round($header-height--sm/2);
		right: 66px;
		border: none;
		border-radius: 0; }

	svg {
		display: block;
		position: absolute;
		@include position(50%);
		// transition
		//$prop: transform, opacity
		//will-change: $prop
		//transition:
		//roperty: $prop
		//uration: $animation-speed
		//iming-function: ease-in-out

		&.svg-ico-search {
			@include size($search-form--btn-icon-size);
			margin: -#{round($search-form--btn-icon-size*0.5)} 0 0 -#{round($search-form--btn-icon-size*0.5)};

			@include media('<tab-lg') {
				@include size($search-form--btn-icon-size--sm);
				margin: -#{round($search-form--btn-icon-size--sm*0.5)} 0 0 -#{round($search-form--btn-icon-size--sm*0.5)}; } }

		//&.svg-ico-cross
		//include size(24px)
		//argin: -12px 0 0 -12px
		//pacity: 0
		/// transform
		//scale: 0.5
		//ransform: scale($scale)
 }		//ransform: scale3d($scale, $scale, 1)

	path {
		fill: currentColor;
		transition: inherit; }

	// hover
	.no-touchevents &:hover {
		color: $base-color; }

	// active
	&.search-is-open {
		display: none; }

	// active
	//&.search-is-open
	//order-color: white

	//vg
	//&.svg-ico-search
	//	opacity: 0
	//	// transform
	//	$scale: 0.5
	//	transform: scale($scale)
	//	transform: scale3d($scale, $scale, 1)

	//&.svg-ico-cross
	//	opacity: 1
	//	// transform
	//	$scale: 1
	//	transform: scale($scale)
 }	//	transform: scale3d($scale, $scale, 1)

.search-closer {
	display: none;
	position: absolute;
	right: nth($page-layout-space, 2);
	top: round($header-height/2);
	margin: -#{round($search-form--opener-size/2)} 0 0;
	height: $search-form--opener-size;
	width: $search-form--opener-size;
	color: $text-color;
	z-index: 20;
	$prop: color;
	transition: {
		property: $prop;
		duration: $animation-speed; }

	@include media('<tab-lg') {
		top: round($header-height--sm/2);
		right: 10px;
		border: none; }

	svg {
		display: block;
		position: absolute;
		@include position(50%);
		@include size(24px);
		margin: -12px 0 0 -12px; }

	path {
		fill: currentColor;
		transition: inherit; }

	// hover
	.no-touchevents &:hover {
		color: $base-color; }

	// active
	&.search-is-open {
		display: block; } }

/* search-form end */
/* === HEADER end === */

/* === NAVIGATION === */
// variables
$nav-is-open-class: 'nav-is-open';

// nav layout
.nav {
	position: fixed;
	width: 100%;
	min-width: $page-layout-min;
	z-index: $z-i-header;
	left: 0;
	top: 0;
	$prop: transform, opacity;
	will-change: $prop;
	transition: {
		property: transform, opacity;
		duration: $animation-speed; }

	@include media('<tab-lg') {
		position: fixed;
		width: $nav-width;
		height: calc(100% - #{$header-height--sm});
		left: 0;
		top: $header-height--sm;
		background-color: white;
		z-index: 9999;
		$x: -100%;
		transform: translate($x, 0); // for old browser
		transform: translateX($x);

		&.#{$nav-is-open-class} { // add js
			$x: 0;
			transform: translate($x, 0); // for old browser
			transform: translateX($x); } }

	@include media('<mob-lg') {
		width: 100%; } }

.nav__layout {
	position: relative;
	height: 100%;
	margin: 0 auto;

	@include media('<tab-lg') {
		padding: 0; } }

/* nav list */
// общие стили для пунктов навигации
.nav__list {
	position: absolute;
	top: 0;
	left: #{nth($logo-size, 1) + nth($page-layout-space, 2) + 50};
	height: $header-height;

	@include media('<tab-lg') {
		position: relative;
		top: auto;
		left: auto;
		height: 100%;
		overflow-x: hidden;
		overflow-y: auto; }

	// убрать подсветку при клике на тачскринах
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li {
		position: relative; }

	.nav__tab {
		position: relative; }

	a {
		display: block;
		position: relative;
		padding: 8px 20px;
		color: $text-color;
		text-decoration: none;
		$prop: color, background-color, border-color;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: $animation-speed; }

		@include media('<tab-lg') {
			@include padding-horizontal(nth($page-layout-space, 2)); }

		//+media('<mob-lg')
		//include padding-horizontal(nth($page-layout-space--mob, 2))

		// убрать подсветку при клике на тачскринах
		.touchevents & {
			user-select: none;
			-webkit-tap-highlight-color: transparent; }

		span {
			@include font(1.6rem, round-float(24/16, 2)); } } }

// общие стили для подменю навигации
.nav__drop {
	display: none;
	// transition
	$prop: opacity, visibility;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: $animation-speed;
		timing-function: ease-in-out; }

	@include media('>=tab-lg') {
		position: absolute;
		left: 0;
		top: 100%;
		width: 280px;
		padding: 14px 0;
		opacity: 0;
		visibility: hidden;
		border: 1px solid #ccc;
		background-color: #fff; }

	.hover > & {
		@include media('>=tab-lg') {
			display: block;
			opacity: 1;
			visibility: visible; } }

	.no-js & {
		display: block;
		opacity: 1;
		visibility: visible; } }

// стили для пунктов навигации, у которых ЕСТЬ ПОДМЕНЮ
.nav__list li.has-drop > .nav__tab > a {
	@include media('<tab-lg') {
		padding-right: (round($nav-arrow-size+10)); } }

// стили для пункта навигации ТЕКУЩЕЙ страницы
.nav__list li.current > .nav__tab {

	& {
		a {
			color: $base-color;
			@include font-alt-bold; } } }

// стили для пункта навигации С ОТКРЫТЫМ ПОДМЕНЮ
//.nav__list li
//.is-open
//& > .nav__tab a
//	&::before
//		opacity: 1
//		transform: translate(0, 0)

//& > .nav__drop
//isplay: block

// нужно добавлять на nav__drop style="display: block"

// особые стили для пунктов навигации ПЕРВОГО урованя
.nav__list > li {
	float: left;
	height: $header-height;

	@include media('<tab-lg') {
		float: none;
		height: auto;
		width: $nav-width;

		.touchevents & {
			width: auto; } }

	& > .nav__tab {
		height: 100%;
		padding: 10px 0;

		@include media('<tab-lg') {
			padding: 0;
			height: auto; }

		a {
			display: flex;
			align-items: center;
			height: 100%;
			@include padding-vertical(5px);
			@include font-alt-bold;
			color: $text-color;
			border-radius: 10px;
			border: 2px solid transparent;

			@include media('<tab-lg') {
				border: none;
				@include padding-vertical(15px); }

			span {
				@include font(2.0rem, round-float(24/20, 2)); } } }

	// hover
	&.hover {
		// только для десктопа
		@include media('>=tab-lg') {
			& > .nav__tab {
				a {
					border-color: rgba($base-color, 0.3);
					color: $base-color; } } } } }

// СТРЕЛКИ навигации
.nav__angle {
	position: absolute;
	right: 0;
	top: 0;
	width: $nav-arrow-size;
	height: 100%;
	//border: solid #666
	//border-width: 0 0 0 1px
	transition-property: transform, color, background-color;
	transition-duration: $animation-speed;
	z-index: 2;
	cursor: pointer;
	display: none;

	// убрать подсветку при клике на тачскринах
	.touchevents & {
		user-select: none;
		-webkit-tap-highlight-color: transparent; }

	li.has-drop > .nav__tab > & {
		@include media('<tab-lg') {
			display: block; } }

	i {
		position: absolute;
		@include position(50%, 50%);
		@include size(20px);
		margin: -10px 0 0 -10px;
		background-color: $base-color;
		border-radius: 10px;
		pointer-events: none;
		transition-property: transform;
		transition-duration: $animation-speed;
		@include font(0, 0);

		&::before, &::after {
			@include pseudo();
			@include position(50%, 50%);
			height: 2px;
			width: 10px;
			margin: -1px -5px;
			border-radius: 2px;
			backface-visibility: hidden;
			background-color: white;
			transition-property: background-color, transform;
			transition-duration: $animation-speed; }

		&::after {
			transform: rotate(0.25turn); } }

