	justify-content: space-between;
	border-top: 1px solid #ddd;
	@include padding-horizontal(nth($page-layout-space, 2));
	@include padding-vertical(1rem);
	min-height: 60px;

	@include media('<tab-lg') {
		@include padding-vertical(20px); }

	@include media('<mob-lg') {
