.footer-col {
	width: 33.33%;
	@include padding-horizontal(round(nth($grid-space, 2)/2));
	// для браузеров не поддерживающих display: flex
	display: inline-block;
	display: flex;
	justify-content: center;
	text-align: center;

	@include media('<mob-lg') {
		display: block;
		width: 100%;
		margin-bottom: 20px;
		padding: 0;

		&:last-child {
			margin-bottom: 0; } }

	.footer-row:first-child & {
		width: auto;

