	&:first-child {
		@include media('>=mob-lg') {
			justify-content: flex-start;
			@include padding-horizontal(0);
			text-align: left; } }

	&:last-child {
		@include media('>=mob-lg') {
			justify-content: flex-end;
			@include padding-horizontal(0);
