/* subscription form */
.subs-form {
	$subs-form-size: 40px 288px; // h w
	$subs-form-space: 8px 10px; // v h
	$subs-form-border: 2px; // border width
	$subs-form-border-radius: 6px;

	text-align: left;

	@include media('<mob-lg') {
		width: 100%; }

	.input-wrap {
		display: flex;
		margin: 0;

		@include media('<mob-lg') {
			display: block; } }

	.label-holder {
		margin: 0 17px 0 0;
		color: #999; }

	.label-holder, label {
		@include font-bold;
		@include font(14px, 40px);
		text-transform: uppercase; }

	.form-fields {
		position: relative;
		width: nth($subs-form-size, 2);

		@include media('<mob-lg') {
			width: 100%; } }

	.input-holder {
		position: relative;
		height: nth($subs-form-size, 1);
		padding-right: 78px;
		$prop: opacity;
		will-change: $prop;
		transition: {
			property: $prop;
			duration: 0.25s; }

		input {
			height: nth($subs-form-size, 1);
			font-size: 14px;
			line-height: nth($subs-form-size, 1) - nth($subs-form-space, 1) * 2 - $subs-form-border * 2;
			padding: $subs-form-space;
			border-radius: $subs-form-border-radius 0 0 $subs-form-border-radius;

			&:-webkit-autofill {
				-webkit-box-shadow: 0 0 0 30px $form-bg-color inset; }

			&:-webkit-autofill {
				-webkit-text-fill-color: $text-color; } } }

	&.success-form .input-holder {
		opacity: 0;
		visibility: hidden;
		$scale: 0.76;
		transform: scale($scale);
		transform: scale3d($scale, $scale, 1);
		pointer-events: none; }

	input[type='submit'], button {
		@include size(80px, nth($subs-form-size, 1));
		min-width: 0;
		font-size: 14px;
		line-height: nth($subs-form-size, 1) - nth($subs-form-space, 1) * 2 - $subs-form-border * 2;
		padding: 0;
		position: absolute;
		top: 0;
		right: 0;
		cursor: pointer;
		box-shadow: none;
		border-radius: 0 $subs-form-border-radius $subs-form-border-radius 0; }

	.subs-success {
		position: absolute;
		@include position(0);
		@include size(100%);
		opacity: 0;
		pointer-events: none;
		background-color: $base-color-alt;
		transform: scale(0.76, 0.76) translateZ(0);
		border-radius: $subs-form-border-radius;
		//text-indent: -9999px
		//overflow: hidden
		text-align: center;
		color: $text-color;
		font-size: 16px;
		line-height: nth($subs-form-size, 1);

		transition: {
			property: opacity, visibility, transform;
			duration: 0.25s; }

		span {
			position: relative;
			display: inline-block;
			vertical-align: top;
			//position: absolute
			//+position(50%)
			//+size(24px, 16px)
			//margin: -8px 0 0 -12px

			&::before, &::after {
				@include pseudo();
				@include size(0, 2px);
				left: 0;
				top: 50%;
				background-color: currentColor;
				$prop: width;
				will-change: $prop;
				transition: {
					property: $prop;
					duration: 0.12s;
					timing-function: linear; } }

			&::before {
				margin: -3px 0 0 -29px;
				transform: rotate(45deg) translateZ(0);
				transform-origin: 0 8px;
				transition-delay: 0.12s; }

			&::after {
				transform: rotate(-50deg) translateZ(0);
				transform-origin: 0 16px;
				margin: -1px 0 0 -8px; } } }

	&.success-form .subs-success {
		opacity: 1;
		visibility: visible;
		transform: scale(1, 1) translateZ(0);

		span {
			&::before {
				width: 8px;
				transition: {
					delay: 0s;
					timing-function: cubic-bezier(0.6, -0.28, 0.74, 0.05); } }

			&::after {
				width: 16px;
				transition-delay: 0.12s; } } }

	.subs-error {
		position: absolute;
		top: 100%;
		left: 0;
		@include size(100%, 14px);
		@include font(10px, 14px);
		color: $form-error-color;
		@include truncate(100%);
		opacity: 0;
		z-index: -1;
		user-select: none;
		pointer-events: none;

		transition: {
			property: opacity;
			duration: 0.1s; } }

	&.error-form .subs-error {
		opacity: 1; } }

/* subscription form end */

