}

body, html {
	height: 100%;
	width: 100%;
}

select[disabled] {
	cursor: default;
}

form {
	margin: 0;
	padding: 0;
}

input {
	vertical-align: middle;
}

textarea, .select select, input[type="text"], input[type="email"], input[type="search"], input[type="submit"], input[type="button"], input[type="reset"], button {
	-webkit-appearance: none;
	-moz-appearance: none;
