/* content img end */
/* columnize */
.columnize, .columnize--3, .columnize--4 {
	column-count: 2;
	column-gap: 2em;
	margin-bottom: 3rem;
	font-size: 0.9em;
}

.columnize--3 {
	column-count: 3;
	font-size: 0.85em;
}

.columnize--4 {
	column-count: 4;
	font-size: 0.8em;
}

@media (max-width: 1199px) {
	.columnize--4 {
		column-count: 3;
	}
}

@media (max-width: 991px) {
	.columnize--3, .columnize--4 {
		column-count: 2;
	}
}

@media (max-width: 767px) {
	.columnize, .columnize--3, .columnize--4 {
		column-count: 1;
	}
}

/* columnize end */
.user-content p + h2,
.user-content ul + h2,
.user-content ol + h2 {
	margin-top: 1.6em;
}

.user-content p + h3,
.user-content ul + h3,
.user-content ol + h3 {
	margin-top: 1.6em;
}

.user-content p + h4,
.user-content ul + h4,
.user-content ol + h4 {
	margin-top: 1.6em;
}

.user-content p + h5,
.user-content ul + h5,
.user-content ol + h5 {
	margin-top: 1.6em;
}

/* list */
.user-content ul, .user-content ol {
	margin: 0 0 3rem;
	padding: 0;
	list-style: none;
}

.user-content ul li, .user-content ol li {
	padding-left: 20px;
	margin: 0 0 1.5rem;
	position: relative;
}

.user-content ul > li::before {
	content: '';
	display: block;
	float: left;
	width: 0;
	height: 0;
	margin: 0.7em 0 -0.6em -20px;
	border-radius: 6px;
	border: solid transparent;
	border-width: 3px;
	border-color: #333;
}

.user-content ol {
	counter-reset: section;
}

.user-content ol > li {
	counter-increment: section;
	height: 1%;
	overflow: hidden;
	padding-left: 40px;
}

.user-content ol > li::before {
	position: relative;
	display: inline-block;
	vertical-align: top;
	width: 28px;
	height: 28px;
	font-family: "Museo Sans Cyrl 700", sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 12px;
	line-height: 28px;
	text-align: center;
	color: white;
	background-color: #00abc5;
	content: counter(section);
	border-radius: 28px;
	padding: 0;
	margin: 1px calc(40px - 28px) 0 -40px;
}

.user-content ul ul, .user-content ul ol, .user-content ol ul, .user-content ol ol {
	margin: 2rem 0;
}

/* list reset */
.reset-list,
.user-content .reset-list {
	list-style: none;
}

.reset-list li, .reset-list li:first-child,
.user-content .reset-list li,
.user-content .reset-list li:first-child {
	padding: 0;
}

.reset-list li::before, .reset-list li:first-child::before,
.user-content .reset-list li::before,
.user-content .reset-list li:first-child::before {
	content: none;
}

/* list reset end */
/* list end */
/* table */
table {
