/* table end */
/* blockquote */
.user-content blockquote {
	display: block;
	margin: 0 0 3rem;
	padding: 0;
	background-color: white;
}

.user-content blockquote q {
	display: block;
	margin: 0;
	padding: 0 0 10px;
	font-family: "Museo Sans Cyrl 700", sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 1.5em;
	line-height: 1.33;
	color: #00abc5;
	text-align: center;
}

.user-content blockquote q::before, .user-content blockquote q::after {
	font-family: "Museo Sans Cyrl 700", sans-serif;
	font-weight: normal;
	font-style: normal;
	content: "";
	display: block;
	position: relative;
	font-size: 1.5em;
	line-height: 1;
	text-align: left;
}

.user-content blockquote q::before {
	content: '“';
	padding-left: 17%;
	padding-bottom: 10px;
}

.user-content blockquote q::after {
	content: '”';
	text-align: right;
	padding-right: 18.5%;
	padding-top: 8px;
}

.user-content blockquote cite {
	text-align: right;
	display: block;
	margin-left: auto;
	font-family: "Museo Sans Cyrl 700 Italic", sans-serif;
	font-weight: normal;
