		justify-content: flex-start;
		padding-right: 0;
		padding-left: 0;
		text-align: left;
	}
}

@media (min-width: 640px) {
	.footer-col:last-child {
		justify-content: flex-end;
		padding-right: 0;
		padding-left: 0;
		text-align: right;
	}
}

.footer-col a {
	color: inherit;
	text-decoration: none;
}

.no-touchevents .footer-col a:hover {
	text-decoration: underline;
}

/* footer navigation */
.footer-nav ul {
	font-size: 1.8rem;
	line-height: 1.67;
	margin-left: -45px;
	margin-bottom: -10px;
}

.footer-nav ul::after {
	content: '';
	display: table;
	clear: both;
}

@media (max-width: 1199px) {
	.footer-nav ul {
		margin-left: -20px;
	}
}

.footer-nav ul li {
	float: left;
	margin: 0 0 10px 45px;
}

@media (max-width: 1199px) {
	.footer-nav ul li {
		margin-left: 20px;
	}
}

@media (max-width: 639px) {
	.footer-nav ul li {
		float: none;
	}
}

.footer-nav ul a {
	text-decoration: none;
	color: #333;
}

.no-touchevents .footer-nav ul a:hover {
	color: #00abc5;
	text-decoration: none;
}

/* footer navigation end */
/* subscription form */
.subs-form {
	text-align: left;
}

@media (max-width: 639px) {
	.subs-form {
		width: 100%;
	}
}

.subs-form .input-wrap {
	display: flex;
	margin: 0;
}

@media (max-width: 639px) {
	.subs-form .input-wrap {
		display: block;
	}
}

.subs-form .label-holder {
	margin: 0 17px 0 0;
	color: #999;
}

.subs-form .label-holder, .subs-form label {
	font-family: "Museo Sans Cyrl 700", sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 14px;
	line-height: 40px;
	text-transform: uppercase;
}

.subs-form .form-fields {
	position: relative;
	width: 288px;
}

@media (max-width: 639px) {
	.subs-form .form-fields {
		width: 100%;
	}
}

.subs-form .input-holder {
	position: relative;
	height: 40px;
	padding-right: 78px;
	will-change: opacity;
	transition-property: opacity;
	transition-duration: 0.25s;
}

.subs-form .input-holder input {
	height: 40px;
	font-size: 14px;
	line-height: 20px;
	padding: 8px 10px;
	border-radius: 6px 0 0 6px;
}

.subs-form .input-holder input:-webkit-autofill {
	-webkit-box-shadow: 0 0 0 30px #f3f3f3 inset;
}

.subs-form .input-holder input:-webkit-autofill {
	-webkit-text-fill-color: #333;
}

.subs-form.success-form .input-holder {
	opacity: 0;
	visibility: hidden;
	transform: scale(0.76);
	transform: scale3d(0.76, 0.76, 1);
	pointer-events: none;
}

.subs-form input[type='submit'], .subs-form button {
	width: 80px;
	height: 40px;
	min-width: 0;
	font-size: 14px;
	line-height: 20px;
	padding: 0;
	position: absolute;
	top: 0;
	right: 0;
	cursor: pointer;
	box-shadow: none;
	border-radius: 0 6px 6px 0;
}

.subs-form .subs-success {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	pointer-events: none;
	background-color: #ffd400;
	transform: scale(0.76, 0.76) translateZ(0);
	border-radius: 6px;
	text-align: center;
	color: #333;
	font-size: 16px;
	line-height: 40px;
	transition-property: opacity, visibility, transform;
	transition-duration: 0.25s;
}

.subs-form .subs-success span {
	position: relative;
	display: inline-block;
	vertical-align: top;
}

.subs-form .subs-success span::before, .subs-form .subs-success span::after {
	content: "";
	display: block;
	position: absolute;
	width: 0;
	height: 2px;
	left: 0;
	top: 50%;
	background-color: currentColor;
	will-change: width;
	transition-property: width;
	transition-duration: 0.12s;
	transition-timing-function: linear;
}

.subs-form .subs-success span::before {
	margin: -3px 0 0 -29px;
	transform: rotate(45deg) translateZ(0);
	transform-origin: 0 8px;
	transition-delay: 0.12s;
}

.subs-form .subs-success span::after {
	transform: rotate(-50deg) translateZ(0);
	transform-origin: 0 16px;
	margin: -1px 0 0 -8px;
}

.subs-form.success-form .subs-success {
	opacity: 1;
	visibility: visible;
	transform: scale(1, 1) translateZ(0);
}

.subs-form.success-form .subs-success span::before {
	width: 8px;
	transition-delay: 0s;
	transition-timing-function: cubic-bezier(0.6, -0.28, 0.74, 0.05);
}

.subs-form.success-form .subs-success span::after {
	width: 16px;
	transition-delay: 0.12s;
}

.subs-form .subs-error {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 14px;
	font-size: 10px;
	line-height: 14px;
	color: #ef5350;
	max-width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	opacity: 0;
	z-index: -1;
	user-select: none;
	pointer-events: none;
	transition-property: opacity;
	transition-duration: 0.1s;
}

.subs-form.error-form .subs-error {
	opacity: 1;
}

/* subscription form end */
/* developer */
.developer {
	display: inline-block;
	line-height: 1;
}

.developer a {
	display: inline-block;
	vertical-align: top;
	text-decoration: none;
}

.developer__label {
	display: inline-block;
	vertical-align: middle;
	margin: 4px 0;
}

@media (max-width: 639px) {
	.developer__label {
		font-size: 1.2rem;
		margin-right: 5px;
	}
}

.developer__logo {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	width: 133px;
	height: 20px;
	text-align: left;
	text-indent: -9999px;
	margin-left: 10px;
}

@media (max-width: 639px) {
	.developer__logo {
		width: 98px;
		height: 16px;
		margin-left: 0;
	}
}

.developer__logo::after {
	content: "";
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #ccc url("data:image/png;base64,") no-repeat center center;
	background-size: auto 17px;
	transition-property: background-color;
	transition-duration: 0.2s;
}

@media (max-width: 639px) {
	.developer__logo::after {
		background-size: auto 12px;
	}
}

.no-touchevents .developer a:hover {
	text-decoration: none;
}

.no-touchevents .developer a:hover .developer__logo::after {
	background-color: #00ef85;
}

/* developer end */
